<template>
  <div>
    <div ref="map" style="height: 100vh;"></div>
    <v-row style="position: absolute; top: 10px; left: 0; width: 100%; padding: 0 40px;">
      <v-col cols="4">

      </v-col>
      <v-col cols="4">
        <v-text-field
          v-model="address"
          outlined
          height="56"
          class="ml-2"
          style="border-radius: 16px; background-color: white; width: 426px; font-size: 18px; line-height: 28px; letter-spacing: 0.5px;"
          hide-details
          placeholder="Search fields"
          append-icon="mdi-magnify"
          @click:append="findAddress"
        />
      </v-col>
      <v-col cols="4">
        <v-card
          height="56"
          width="218"
          style="border-radius: 16px; float: right; padding: 10px 16px; padding-left: 8px;"
        >
          <div style="display: flex; justify-content: space-between;">
            <v-btn @click="helpModal = true" width="35" height="35" icon>
              <v-icon>mdi-help-circle-outline</v-icon>
            </v-btn>
            <v-btn @click="analyse" rounded depressed color="#015924" style="padding: 12px;"><span style="color: white">Analyse turfs</span></v-btn>
          </div>
        </v-card>
      </v-col>
    </v-row>
    <v-dialog width="878" v-model="analysing">
      <v-card style="padding: 56px; border-radius: 16px;">
        <h1>Analysing turfs</h1>
        <p style="margin-top: 32px; font-size: 16px; letter-spacing: 0.15px;">Please wait, it may take up to 10 minutes</p>
      </v-card>
    </v-dialog>
    <v-dialog width="878" v-model="error">
      <v-card style="padding: 56px; border-radius: 16px;">
        <v-icon style="font-size: 85px; color: red;" x-large>mdi-alert-circle-outline</v-icon>
        <h1 style="margin-top: 20px;">Oops! Something went wrong.</h1>
        <v-btn rounded depressed color="#015924" style="padding: 12px; margin-top: 40px;"><span style="color: white">Try again</span></v-btn>
      </v-card>
    </v-dialog>
    <v-dialog width="878" v-model="helpModal">
      <v-card style="padding: 56px; border-radius: 16px;">
        <h1>Need help?</h1>
        <div style="text-align: left;">
          <h5 style="margin-top: 32px; font-weight: normal;">Step 1</h5>
          <p style="margin-top: 4px;">Insert or pick a location on the map around which you wish to find football fields.</p>
          <h5 style="margin-top: 8px; font-weight: normal;">Step 2</h5>
          <p style="margin-top: 4px;">Click on analyse turfs button and we will check the surrounding area whithin 5 km radius for football fields.</p>
        </div>
        <v-btn rounded depressed color="#015924" style="padding: 12px; margin-top: 40px;"><span style="color: white">Got it</span></v-btn>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  name: 'Request',
  data: () => ({
    map: null,
    geocoder: null,
    marker: null, // marker.getPosition().lat(), ...lng() to get coordinates
    address: '',
    analysing: false,
    error: false,
    helpModal: false
  }),
  methods: {
    initializeMap() {
      this.geocoder = new window.google.maps.Geocoder();
      this.map = new window.google.maps.Map(this.$refs.map, {
        zoom: 14,
        center: { lat: 	59.436962, lng: 24.753574 }, // could be users location
        mapTypeId: "hybrid",
        clickableIcons: false,
        disableDefaultUI: true
      });
      this.map.addListener('click', e => this.putMarkerOnMap(e.latLng));
    },
    putMarkerOnMap(coordinates) {
      this.marker && this.marker.setMap(null);
      this.marker = new window.google.maps.Marker({
        position: coordinates,
        map: this.map,
        draggable: true
      });
    },
    findAddress() {
      this.geocoder.geocode({ 'address': this.address }, (results, status) => {
        if (status === 'OK') {
          this.putMarkerOnMap(results[0].geometry.location);
          this.map.panTo(this.marker.getPosition());
        } else {
          console.log('Fail: ' + status);
        }
      });
    },
    analyse() {
      // make request
      this.analysing = true;
      console.log(`Here request with these coordinates: ${this.marker.getPosition().lat()}, ${this.marker.getPosition().lng()}`);
    
    }
  },
  mounted() {
    window.initMap = this.initializeMap;
    const mapScript = document.createElement('script');
    mapScript.setAttribute('src', 'https://maps.googleapis.com/maps/api/js?key=AIzaSyBxa4bpZfV-XTrtvLuQyfYXuVx4GRMWf2A&callback=initMap&libraries=&v=weekly');
    document.head.appendChild(mapScript);
  }
}
</script>

